import React from "react";
import { Link } from "react-router-dom";

import MenuItemAccordion from "../menuItemAccordion/MenuItemAccordion";

import "./menu.scss";

const Menu = ({ toggleMenu }) => {
	return (
		<div className="menu">
			<div className="menu__top top-menu">
				<ul className="top-menu__list">
					<li className="top-menu__item">
						<Link
							className="top-menu__link"
							to="/anypage"
							aria-label="перейти на сторінку - Всі новини"
							onClick={toggleMenu}
						>
							Всі новини
						</Link>
					</li>
					<li className="top-menu__item">
						<Link
							className="top-menu__link"
							to="/anypage"
							aria-label="перейти на сторінку - COVID-19"
							onClick={toggleMenu}
						>
							COVID-19
						</Link>
					</li>
					<li className="top-menu__item">
						<Link
							className="top-menu__link"
							to="/anypage"
							aria-label="перейти на сторінку - Війна"
							onClick={toggleMenu}
						>
							Війна
						</Link>
					</li>
				</ul>
			</div>
			<div className="menu__middle middle-menu">
				<MenuItemAccordion
					title="Політика"
					links={[
						{ label: "Україна", url: "#" },
						{ label: "Світ", url: "#" },
					]}
					toggleMenu={toggleMenu}
				/>
				<MenuItemAccordion
					title="Здоров’я"
					links={[
						{ label: "Дієти", url: "#" },
						{ label: "Краса", url: "#" },
						{ label: "Секс", url: "#" },
						{ label: "Психологія", url: "#" },
						{ label: "Медицина", url: "#" },
					]}
					toggleMenu={toggleMenu}
				/>
				<MenuItemAccordion
					title="Події"
					links={[
						{ label: "ДТП", url: "#" },
						{ label: "Катастрофи", url: "#" },
						{ label: "Кримінал", url: "#" },
					]}
					toggleMenu={toggleMenu}
				/>
				<MenuItemAccordion
					title="Лайфстайл"
					links={[
						{ label: "Історії з життя", url: "#" },
						{ label: "Подорожі", url: "#" },
						{ label: "Кухня", url: "#" },
						{ label: "Шоубізнес", url: "#" },
						{ label: "Мода та стиль", url: "#" },
						{ label: "Зірковий гороскоп", url: "#" },
					]}
					toggleMenu={toggleMenu}
				/>
				<MenuItemAccordion
					title="Суспільство"
					links={[
						{ label: "Охорона здоров’я", url: "#" },
						{ label: "Безпека", url: "#" },
						{ label: "Екологія", url: "#" },
						{ label: "Релігія", url: "#" },
					]}
					toggleMenu={toggleMenu}
				/>
				<MenuItemAccordion
					title="Регіони"
					links={[
						{ label: "Київ", url: "#" },
						{ label: "Одеса", url: "#" },
						{ label: "Харків", url: "#" },
						{ label: "Львів", url: "#" },
					]}
					toggleMenu={toggleMenu}
				/>
				<MenuItemAccordion
					title="Техно"
					links={[
						{ label: "Наука", url: "#" },
						{ label: "Гаджети", url: "#" },
						{ label: "ІТ", url: "#" },
						{ label: "Космос", url: "#" },
					]}
					toggleMenu={toggleMenu}
				/>
			</div>
		</div>
	);
};

export default Menu;
