import React from "react";
import { Link } from "react-router-dom";

import "./anyPage.scss";

const AnyPage = () => {
	return (
		<div className="any-page">
			<div className="container">
				<img
					className="any-page__image"
					src="https://digidpr.in/wp-content/uploads/2021/06/web-main.gif"
					alt="Технічні працівники займаються створенням нових сторінок для цього веб додатку"
				/>
				<p className="any-page__title">
					Це <span>НЕ</span> помилка!
				</p>
				<p className="any-page__subtitle">
					На жаль, наразі існує лише одна сторінка на цьому сайті. <br /> P.S. І ви її щойно покинули...
				</p>
				<Link to="/" className="any-page__link" aria-label="Повернутися попередню сторінку" replace>
					Повернутися назад
				</Link>
			</div>
		</div>
	);
};

export default AnyPage;
