import React from "react";
import { Link } from "react-router-dom";

import MoreButton from "../moreButton/MoreButton";

import "./cardRegion.scss";

const CardRegion = ({ city }) => {
	return (
		<div className="card-region">
			<h3 className="card-region__title">{city}</h3>
			<div className="card-region__content">
				<div className="card-region__news">
					<div className="card-region__day">
						<time className="card-region__news-date" dateTime="2022-08-05">
							05 серпня
						</time>
						<ul className="card-region__list">
							<li className="card-region__item block-news">
								<Link to="/anypage" className="block-news__link">
									<time dateTime="2022-08-05T14:59">14:59</time>
									ТРЦ Ocean Plaza після чуток про закриття оголосив про відновлення роботи
								</Link>
							</li>
							<li className="card-region__item block-news">
								<Link to="/anypage" className="block-news__link">
									<time dateTime="2022-08-05T14:59">14:59</time>
									На Київщині загиблого під час окупації чоловіка знайшли у колодязі: його прикмети
								</Link>
							</li>
						</ul>
					</div>
					<div className="card-region__day">
						<time className="card-region__news-date" dateTime="2022-08-05">
							05 серпня
						</time>
						<ul className="card-region__list">
							<li className="card-region__item block-news">
								<Link to="/anypage" className="block-news__link">
									<time dateTime="2022-08-05T14:59">14:59</time>
									КМДА контролюватиме якість підготовки до опалювального сезону
								</Link>
							</li>
							<li className="card-region__item block-news">
								<Link to="/anypage" className="block-news__link">
									<time dateTime="2022-08-05T14:59">14:59</time>
									Дружина Зеленського чесно зізналася, чи боїться смерті
								</Link>
							</li>
							<li className="card-region__item block-news">
								<Link to="/anypage" className="block-news__link">
									<time dateTime="2022-08-05T14:59">14:59</time>
									ТРЦ Ocean Plaza після чуток про закриття оголосив про відновлення роботи
								</Link>
							</li>
						</ul>
					</div>
				</div>
			</div>
			<MoreButton className={"card-region__more"} label={"Більше новин"} url={"#"} />
		</div>
	);
};

export default CardRegion;
